body {
    background-color: #EEEEEE;
}


/* Navigation/Heading Section */
.navbar {
    z-index: 100;
    align-items: center;
    background-color: #3A98B9;
}

.navbar img {
    height: 6rem;
}

.navbar-expand-lg {
    padding: 0.8rem;
    background-color: #5ebcde;
}

.navbar-expand-lg .container-fluid img {
    height: 0rem;
    visibility: hidden;
}

.nav-item {
    padding-right: 8px;
}

.navbar-toggler {
    margin-left: -1rem;
}

.dropdown-menu {
    background-color: #5eb5d4a5;
}

.About {
    text-align: center;
}

.icon {
    width: 1.5rem;
    height: 1rem;
}

/* Body Section */
.row {
    margin-top: 1rem;
    width: 100%;
}

.card-group {
    margin: 2rem;
}

.card-text {
    text-align: justify;
    padding: 1rem;
}

.About-School {
    background-color: #EEEEEE;
    border-top: 2px #3A98B9 solid;
    border-radius: 6px;
}

.notification {
    background-color: #d3d1d1;
    border-top: 2px #3A98B9 solid;
    border-radius: 6px;
    padding: 2rem;
}

.notification:hover {
    background-color: #EEEEEE;
}

.School_Head {
    background-color: #FFF1DC;
    border-top: 2px #3A98B9 solid;
    margin-top: 1rem;
    padding: 2rem 7rem;
}

.School_Head img {
    border-radius: 50%;
}

.RegForm {
    margin: 2rem 7rem;
    padding: 1rem 3rem;
}

/* Endbar/Footer Section */
.Footer {
    background-color: #3A98B9;
    text-align: center;
    color: rgb(255, 255, 255);
}
iframe{
    width: 24rem;
    height: 13rem;
}
#end-text {
    font-size: smaller;
    color: black;
}

#end-text a {
    color: blue;
}

.custom-width-btn {
    background-color: #3A98B9;
}
span{
    color: red;
}


/* Admin section */
aside{
    background-color: #EEEEEE;
}
.bd-aside .btn:focus{
    box-shadow: 0 0 0 1px rgba(121, 82, 179, .7);
}
.bd-aside .btn:hover, .bd-aside .btn:focus{
    color: rgba(0, 0, 0, .85);
    background-color: rgba(121, 82, 179, .1);
}
Link:focus:not(:focus-visible) {
    outline: 0;
}
.bd-aside .btn {
    padding: .25rem .5rem;
    font-weight: 600;
    color: rgba(0, 0, 0, .65);
}



/* Media Query */
@media (max-width: 991.5px) {
    .navbar .navbar-brand {
        display: none;
    }

    #carouselExampleFade .carousel-item img {
        height: 25vh;
    }

    .navbar-expand-lg {
        padding: 0.3rem;
    }

    .navbar-expand-lg .container-fluid img {
        height: 8vh;
  
        visibility: visible;
    }

    .row {
        width: 102%;
        font-size: 15px;
        margin-top: 0.4rem;
    }

    .card-text {
        text-align: justify;
        padding: 0;
    }

    .School_Head {
        background-color: #FFF1DC;
        padding: 0rem 0.8rem;
    }
    iframe{
        width: 24rem;
        height: 13rem;
    }

}
@media (max-width: 650px) {
    .RegForm{
        margin: 0.5rem;
    }
    iframe{
        width: 50vw;
        height: 22vh;
    }
}